.Model{
    position: fixed;
    z-index: 500;
    background-color: bisque;
    width: 70%;
    border:1px solid #ccc;
    box-shadow: 1px 1px 1px rgb(20, 15, 15);
    padding: 16px;
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}
@media (min-width:600px) {
    .Model{
        width: 600px;
        left: calc(25%-250px);
        height: 400px;
    }
}